<template>
  <div>
    <gmap-map v-bind="options" id="map"></gmap-map>
  </div>
</template>

<script>
import { gmapApi } from "vue2-google-maps";
export default {
  name: "GoogleMap",

  data() {
    return {
      options: {
        zoom: 12,
        center: {
          lat: 50.619393,
          lng: 26.252786
        }
      }
    };
  },
  computed: {
    google: gmapApi
  }
};
</script>
<style scoped>
#map {
  width: 100%;
  height: 100%;
}

@media only screen and (max-width: 575px) {
  #map {
    width: 290px;
    height: 380px;
    margin: 0 auto;
    overflow: hidden;
  }
}

@media only screen and (min-width: 576px) and (max-width: 992px) {
  #map {
    width: 310px;
    height: 400px;
    margin: 0 auto;
    overflow: hidden;
  }
}

@media only screen and (min-width: 992px) {
  #map {
    width: 400px;
    height: 500px;
    margin: 0 auto;
    overflow: hidden;
  }
}
</style>